*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    
}

.header{
    text-align: left;
    justify-content: center;
    align-items: center;
    margin: 30px;
    color: red;
    display: flex;
    column-gap: 70%;

}

.headr{
    text-align: right;
    display: flex;
    right: 0;
    justify-content: right;
    column-gap: 20px;
    text-decoration: unset;
}

.about{ 
    align-items: center;
    justify-content: center;
    margin: 10%;
}

.about h6{
    display: flex;
    background-color:#2980b9 ;
    font-style: italic;
    font-size: large;
    margin-bottom: 20px;
}

.about p{
    margin-left: 2rem;
}

.menu{
    justify-content: center;
    align-items: center;
    margin: 5%;
}

.menu h5{
    font-weight: bold;
    margin-bottom: 1rem;
    background-color: #2980b9;
}

button{
    height: 30px;
    width: 100px;
    margin: 6px;
    margin-left: 30px;
    cursor: pointer;
    text-align: center;
}

#create{
    border-radius: 4px;
    padding:.7em 1.5em;
    margin-left: 30px;
}

#create h1{
    background-color: #2980b9;
    margin-bottom: 30px;
}

.user button{
    justify-content: center;
    background-color: springgreen;
    align-items: center;
    margin-top: 20px;
    margin-left: 40%;
}

label{
    margin: 3%;
    grid-template-columns: 3fr 3fr;
    align-items: center;
    justify-content: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
}

input{
    margin: 2%;
    align-items: center;
    justify-content: center;
    border-radius: 1%;
    height: 25px;

}

#datas{
    display: grid;
    grid-template-columns: 3fr 2fr 2fr 2fr;
    margin: 5%;
    row-gap: 20px;
    column-gap: 20px;
    border:  4px solid saddlebrown;
    background-color: rgb(192, 255, 255);
        
}

#datas p{
    font-weight: 500;
    display: flex;
    margin: 5px;
}

#datas span{
    font-weight: normal;
    font-size: smaller;
    margin-left: 10px;
}

.alldata{
    margin: 50px;
    padding: auto;
    width: 100%;
    
}

.alldata h4{
    background-color: #2980b9;
    color: black;
}

.id{
    margin-top: 25px;
    
}

#edit{
    text-decoration: none;
    margin: 70px;
}

.editform{
    display: grid;
    grid-template-columns: 3fr 1fr 3fr;
}

.editform input{
    margin-top: 10px;
}

